












export default {
	name: 'rui-navber',
	props: {
		color: {
		  type: String,
		  default: '#000000'
		},
		colorActive: {
		  type: String,
		  default: '#2ed39e'
		},
		status: {
		  type: Number,
		  default: 0
		},
		navberList: {
		  type: Array,
		  default: []
		}
	},
	data() {
		return {
			navberStyle: 'color:' + this.color,
			navberStyleActive: 'color:' + this.colorActive,
			curStatus: this.status
		}
	},
	created(){
		console.log('rui-navber 组件加载完成！')
	},
	methods: {
		navbertap(status){
			// console.log(status)
			this.curStatus = status;
			this.$emit('navbertap', status);
		}
	}
}
